import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faHome,
  faInfoCircle,
  faUser,
  faCog,
  faServer,
  faCloud,
} from "@fortawesome/free-solid-svg-icons";

interface NavItem {
  name: string;
  href: string;
}

interface SideBarProps {
  navItems: NavItem[];
}

const SideBar: React.FC<SideBarProps> = ({ navItems }) => {
  const icons = [faHome, faInfoCircle, faCog, faUser, faServer, faCloud];

  return (
    <div className="flex flex-col items-center justify-between h-screen w-56 bg-primary text-sizeColor position-fixed">
      {" "}
      <div className="flex flex-col items-center space-y-4 mt-5 flex-grow">
        {navItems.map((item, index) => (
          <a
            key={index}
            href={item.href}
            className="text-center text-secondary hover:text-sizeColor font-poppins text-sm pt-3 flex items-center space-x-2 w-full"
          >
            <FontAwesomeIcon icon={icons[index]} className="mr-2" />
            <span className="flex-grow text-left">{item.name}</span>
          </a>
        ))}
      </div>
      <p className="text-center text-secondary font-poppins text-sm mb-5">
        Created by CodeRequest
      </p>
    </div>
  );
};

export default SideBar;
